<template>
  <main>
    <div class="project">
      <div class="project-info loading-animation" v-for="(project, idx) in projectList" :key="idx">
          <img v-if="project.thumbnail" class="project-img" :src="require(`@/assets/img/${project.thumbnail}`)" />
          <div class="project-platform">{{ project.platform }}</div>
          <div>
            <h1 class="project-title">{{ project.title }}</h1>
            <span class="project-subtitle" v-if="project.subtitle">{{ project.subtitle }}</span>
          </div>
          <div class="project-description">
              <span>{{ project.description }}</span>
              <span class="point">{{ project.descriptionPoint }}</span>
          </div>
          <div class="project-date">{{ project.date }}</div>
          <div>
            <button class="btn-link mr5" v-if="project.thumbnails" @click="showThumbnails(project.thumbnails)">화면</button>
            <button class="btn-link mr5" v-if="project.link" @click="showLink(project.link)">ETC</button>
            <button class="btn-link mr5" v-if="project.notion" @click="showLink(project.notion.value)">{{project.notion.label}}</button>
          </div>
          
          <ul class="project-role">
              <li v-for="(role, rIdx) in project.roleList" :key="rIdx">{{ role }}</li>
          </ul>
          <ul class="project-role">
              <li v-for="(res, resIdx) in project.resList" :key="resIdx" style="font-weight:bold">{{ res }}</li>
          </ul>
          <span class="project-language radius">
              <div v-for="(skill, sIdx) in project.skills" :key="sIdx">{{ skill }}</div>
          </span>
          <span class="project-skill radius">
              <div v-for="(tech, tIdx) in project.techList" :key="tIdx">{{ tech }}</div>
          </span>
          <img-viewer ref="viewer"/>
      </div>
      <div class="project-info">
        <div>
          <div style="color:#b4b4b4">* 통합 관제 시스템 *</div>
          <div class="project-subtitle">NMS: Network Management System</div>
          <div class="project-subtitle">SMS: Server Management System</div>
          <div class="project-subtitle">FMS: Facility Management System</div>
          <div class="project-subtitle">WNMS: Wireless Network Management System</div>
          <br/>
          <h1 class="project-title">그 외 프로젝트</h1>
        </div>
        <ul>
          <li v-for="(sub, spIdx) in subProjectList" :key="spIdx">
            {{ sub.title }}
            <div class="sub-project-date">{{ sub.date }}</div>
          </li>
        </ul>
      </div>
    </div>
  </main>
</template>

<script>
import ImgViewer from '@/components/ImgViewer.vue'
import projectData from '../js/projectData'
import subProjectData from '../js/subProjectData'

export default {
  components: {
    ImgViewer
  },
  data() {
    return {
      projectList: projectData,
      subProjectList: subProjectData,
      
    }
  },
  methods: {
    showThumbnails(list) {
      
      let sourceImages = [];
      
      for(const img of list) {
        sourceImages.push({
          thumbnail: `img/site/${img}.png`,
          source: `img/site/${img}.png`,
        });
      }
      
      this.$refs.viewer[0].show(sourceImages, 0);
    },

    showLink(link) {
        window.open(link)
    }
  }
}
</script>

<style></style>